import React, { Component, Fragment } from "react";
import axios from "axios";
import {Link} from 'react-router-dom'
import ReactPaginate from 'react-paginate';
import ProductosEcommerce from './ProductosEcommerce'
import ProductoDetail from './ProductoDetail'

export default class RubrosEcommerce extends Component {
  constructor(props) {
    super(props);

    this.state = {
      offset: 0,
      perPage: 30,
      currentPage: 0,
      data: [],
      search: '',
      link: this.props.Menu,
      value: this.props.SubRubro,
      detail: '',
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  receivedProducto() {
    axios.get(`https://www.sundelimpieza.com/api/productos/producto/${this.state.link}`).then((res) => { // http://localhost:4000
      
    const data = res.data;
      const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
      
      if (data.length === 0) {

        const postProducto = () => (
          <React.Fragment>
            <div>Data</div>
          </React.Fragment>
        )

        this.setState({
          pageCount: Math.ceil(data.length / this.state.perPage),
          postProducto,
        });
        
      }else{
        const postProducto = slice.map((pd) => (
          <React.Fragment>
              <div>
                <div className="uk-card uk-card-default uk-card-small uk-card-body uk-text-center">
                  <img
                    src={"static/images/" + pd._id.Foto}
                    alt="Producto SundeLimpieza"
                    className="uk-max-width"
                  />
                  <h5 className="uk-text-capitalize">{pd._id.Producto}</h5>
                  <Link to="#" onClick={this.handleClickDetail}>
                    <button className="uk-button uk-button-primary uk-button-small" id={pd._id.Producto}>Ver Detalle</button>
                  </Link>
                </div>
              </div>
          </React.Fragment>
        
        ));
        
        this.setState({
          pageCount: Math.ceil(data.length / this.state.perPage),
          postProducto,
        });
      }
      
      
    });
  }

  receivedRubro() {
    axios.get(`https://www.sundelimpieza.com/api/productos/subrubro`).then((res) => {
      const data = res.data;
      const postRubro = data.map((pd) => (
        <React.Fragment>
          <div className="uk-padding-list">
            <Link to="#" onClick={this.handleClick}>
              <li className="uk-text-capitalize uk-text-break uk-text-small" id={pd._id.SubRubro}>{pd._id.SubRubro}</li>
            </Link>
          </div>
        </React.Fragment>
      ));

      this.setState({
        postRubro,
      });
    });
  }

  componentDidMount() {
    this.receivedProducto();
    this.receivedRubro();
  }

  handleClick(e) {
    this.setState({ value: e.target.getAttribute('id') });
  }

  handleChange(e) {
    this.setState({ search: e.target.value });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({ value: this.state.search });
  };

  handleClickDetail = (e) => {
    this.setState({ detail: e.target.getAttribute('id') });
  }

  handlePageClick = (e) => {
    const selectedPage = e.selected;
    const offset = selectedPage * this.state.perPage;

    this.setState({
        currentPage: selectedPage,
        offset: offset
    }, () => {
        this.receivedProducto()
    });
  };

  render() {
    return (
      <div className="uk-container">
        
          {
              this.state.value ? 
                <ProductosEcommerce Menu={this.state.value}/>
              :
                this.state.detail ?
                  <ProductoDetail Producto={this.state.detail} />
                :
                  <Fragment>
              
                    <div className="uk-text-center" uk-grid="true">
                        <div className="uk-width-expand@m">
                          <h4 className="uk-text-bolder uk-text-left uk-margin-top">Listado de Productos:</h4>
                          <div className="uk-child-width-1-3@s uk-child-width-1-4@m uk-grid-small uk-grid-match" uk-grid="true">
                            {this.state.postProducto}
                          </div>
                          <div className="uk-padding uk-padding-remove-horizontal">
                            <ReactPaginate
                              previousLabel={"Anterior"}
                              nextLabel={"Siguiente"}
                              breakLabel={"..."}
                              breakClassName={"break-me"}
                              pageCount={this.state.pageCount}
                              marginPagesDisplayed={2}
                              pageRangeDisplayed={5}
                              onPageChange={this.handlePageClick}
                              containerClassName={"pagination"}
                              subContainerClassName={"pages pagination"}
                              activeClassName={"active"}
                            />
                          </div>
                        </div>
                        <div className="uk-width-1-4@m">
                            <div className="uk-card uk-card-default uk-card-body">
                              <h5 className="uk-text-bolder uk-text-center uk-margin-top">Buscador de Producto:</h5>
                              <form className="uk-search uk-search-small" onSubmit={this.handleSubmit}>
                                <input className="uk-search-input" type="search" placeholder="...Ingresar Busqueda...." onChange={this.handleChange}/>
                              </form>
                              <hr className="uk-divider-icon"></hr>
                              <h5 className="uk-text-bolder uk-text-center uk-margin-top">Listado de Rubros:</h5>
                              <ul className="uk-list uk-text-center">
                                {this.state.postRubro}
                              </ul>
                            </div>
                        </div>
                    </div>
                  </Fragment>
          }
        
      </div>
    );
  }
}
