import React, { useState, Fragment } from "react";
import { connect } from "react-redux";

import { Link } from "react-router-dom";
import Cookies from "universal-cookie";
import { isUndefined } from "util";

import ProductosEcommerceMenu from "./ProductosEcommerceMenu";

import getRubrosActions from "../redux/actions/rubrosActions";
import setUserData from "../redux/actions/productosDetailActions";

const cookies = new Cookies();

const ProductoDetail = (props) => {
  
  const [state, setState] = useState({
    data: [],
    detail: props.Producto,
    count: 0,
    link: "",
    search: "",
    counter: 1,
  });

  const handleClick = (e) => {
    setState({ link: e.target.getAttribute("id") });
  }

  const handleChange = (e) => {
    setState({ search: e.target.value });
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    setState({ link: state.search });
  };

  const handleSubmitProducto = (e) => {
    e.preventDefault();
    setState({ counter: state.counter });
    var clave = e.target.getAttribute("id");
  };

  return(
    <div className="uk-container">
      {state.link ? (
        <ProductosEcommerceMenu Menu={state.link} />
      ) : (
        <Fragment>
          <div className="uk-text-center" uk-grid="true">
            <div className="uk-width-expand@m">
              <h4 className="uk-text-bolder uk-text-left uk-margin-top">
                Detalle del Producto:
              </h4>
              <div
                className="uk-child-width-1-3@s uk-child-width-1-4@m uk-grid-small"
                uk-grid="masonry: true"
              >
                {
                  props.productos.map((data) => (
                    <Fragment>
                      <div>
                        <div className="uk-card uk-card-default uk-card-small uk-card-body uk-text-center uk-text-capitalize" key={data._id}>
                          <h5 className="uk-text-capitalize">{data.Producto}</h5>
                          <p className="uk-text-capitalize">{data.Detalle}</p>
                          <img
                            src={"static/images/" + data.Foto}
                            alt="Producto SundeLimpieza"
                            className="uk-max-width"
                          />
                          
                          <hr className="uk-divider-small"></hr>
                          { 
                            isUndefined(cookies.get("_s")) ? 
                              <p className="uk-card-title">Sin acceso</p> 
                            : 
                              <Fragment>
                                <form onSubmit={handleSubmitProducto} id={data._id}>
                                  
                                  <p className="uk-text-small">
                                    $ {
                                      data.Precio 
                                    }
                                  </p>

                                  {/*
                                  <div class="uk-margin">
                                      <input class="uk-input uk-form-width-medium uk-form-small uk-form-width-xsmall uk-text-center" type="number" onChange={handleChangeProducto} />
                                  </div>
          
                                  <button type="submit" className="uk-button uk-button-primary">Agregar</button>
                                  */}

                                </form>
                              </Fragment>
                          }
                        </div>
                      </div>
                    </Fragment>
                  ))
                }
              </div>
            </div>
            <div className="uk-width-1-4@m">
              <div className="uk-card uk-card-default uk-card-body">
                <h5 className="uk-text-bolder uk-text-center uk-margin-top">
                  Buscador de Producto:
                </h5>
                <form
                  className="uk-search uk-search-small"
                  onSubmit={handleSubmit}
                >
                  <input
                    className="uk-search-input"
                    type="search"
                    placeholder="...Ingresar Busqueda...."
                    onChange={handleChange}
                  />
                </form>
                <hr className="uk-divider-icon"></hr>
                <h5 className="uk-text-bolder uk-text-center uk-margin-top">
                  Listado de Rubros:
                </h5>
                <ul className="uk-list uk-text-center">
                  {
                    props.subrubros.map(subrubro =>
                      <div className="uk-padding-list" key={subrubro._id}>
                        <Link to="#" onClick={handleClick}>
                          <li
                            className="uk-text-capitalize uk-text-break uk-text-small"
                            id={subrubro.SubRubro}
                          >
                            {subrubro.SubRubro}
                          </li>
                        </Link>
                      </div>
                    )
                  }
                </ul>
              </div>
            </div>
          </div>
        </Fragment>
      )}
    </div>
  );

}

const mapDispatchToProps = (dispatch, props) => {
  dispatch(getRubrosActions)
  dispatch(setUserData(props.Producto))
  return {
    fetchRubros: () => dispatch(getRubrosActions),
    setUserData: (detail) => dispatch(setUserData(detail))
  };
};

const mapStateToProps = (state) => {
  return {
    subrubros: state.rubros.array,
    subrubrosfetching: state.rubros.isFetching,
    productos: state.productosDetail.array,
    productosfetching: state.productosDetail.isFetching,
  };
};


export default connect(mapStateToProps, mapDispatchToProps)(ProductoDetail);


